    margin-right: 0.2rem;
    border-radius: 0.4rem;
    border: none;
    color: #FFF;
    box-shadow: rgba(50, 50, 105, 0.15) 0px 2px 5px 0px, rgba(0, 0, 0, 0.05) 0px 1px 1px 0px;
} */
#low{
    /* border: 1px rgb(0, 0, 0) solid; */
    background-color: rgb(246, 221, 0);
    width: 4.5rem;
    height: 1.6rem;
    margin-right: 0.2rem;
    border-radius: 0.4rem;
    border: none;
    color: #FFF;
    box-shadow: rgba(50, 50, 105, 0.15) 0px 2px 5px 0px, rgba(0, 0, 0, 0.05) 0px 1px 1px 0px;

}
#high{
    background-color: rgba(255, 0, 0, 0.838);
    width: 4.5rem;
    height: 1.6rem;
    margin-right: 0.2rem;
    border-radius: 0.4rem;
    border: none;
    color: #FFF;
    box-shadow: rgba(50, 50, 105, 0.15) 0px 2px 5px 0px, rgba(0, 0, 0, 0.05) 0px 1px 1px 0px;
}
#medium{
    background-color: rgba(70, 204, 70, 0.866);
    width: 4.5rem;
    height: 1.6rem;
    margin-right: 0.2rem;
    border-radius: 0.4rem;
    border: none;
    color: #FFF;
    box-shadow: rgba(50, 50, 105, 0.15) 0px 2px 5px 0px, rgba(0, 0, 0, 0.05) 0px 1px 1px 0px;
}
#low:hover{
    color:rgb(246, 221, 0);
    background-color: #FFF;
}
#medium:hover{
    color:rgba(70, 204, 70, 0.866);
    background-color: #FFF;
}
#high:hover{
    color:rgba(255, 0, 0, 0.838);
    background-color: #FFF;
}
#low.click { color: rgb(246, 221, 0); background-color: #FFF; }
#medium.click { color: rgba(70, 204, 70, 0.866); background-color: #FFF; }
#high.click { color: rgba(255, 0, 0, 0.838); background-color: #FFF; }

#input_context{
    box-sizing: border-box;
    margin: 3rem;
}
#input_context>h1{
    margin-bottom: 2.5rem;
}
#cancel{
    all:unset;
    width: 59.3px;
    height: 59.3px;
    
}

#submit{
    all:unset;
    width: 60px;
    height: 60px;
    

}

#form-btn{
    display: flex;
    justify-content: flex-end;
    gap: 2rem;
    margin: 3rem 4rem;
    
}
#pri-value{
    display: none;
}

#todo{
    width: 8rem;
    height: 2rem;
    background-color: #1c1fea;
    display: flex;
    width: 50rem;
    height: 3rem;
    border: none;
    border-radius: 0.7rem;
    background: linear-gradient(90deg, #493E48 0%, #935ACC 67.52%, #E339CD 100%);
    color: #FFF;
    font-size: medium;   
    font-family:"Mada", serif;
    align-items: center;
    margin:1rem 3rem;
    flex-direction: row;
}

#Todo-titles{
    height: fit-content;
}

#main-page{
    display: flex;
    flex-direction: column;
    margin-left:20rem ;
}
#myCheckbox{
    margin-right: auto;
    width: 1.7rem;
    margin: 1rem;
    
    cursor: pointer;
    
    
}
#myCheckbox:checked {
    accent-color: #ffffff; 
    
    
}

#todo >button,.btn{
    background-color: transparent;
    border: none;
    
}
#info{
    height:32px;
    margin-left: auto;
    padding-right: 1rem;
}
#del{
    margin-right: 2rem;
}

#Info{
    border-radius: 2rem;
    height: 15rem;
    width: 25rem;
    /* display: hidden; */
    /* flex-direction: row; */
    border-radius: 2.875rem;
    border: 0px solid #000;
    background: #ffffff;
    flex-shrink: 0;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    
    

}

#Info::backdrop{
    background-color: #00000056;
}
#Info-div{
    margin: 3rem 0rem 3rem 3rem;
    margin-right: auto; 
    font-family:"Mada", serif;
   
}
#info-close{
    position: fixed;
    border: none;
    height: 40px;
    margin: 1rem 1rem 0rem 0rem;
    
    
    
}
#addDialog{
    border: none;
    border-radius: 0.5rem;
    font-family: "Roboto", serif;
    /* position:fixed; */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: white;
    padding: 20px;
    z-index: 1000;
    width: 20rem;
    /* height: 8rem; */
}

#addDialog::backdrop{
    background-color: #00000056;
}
#addProject{
    width: 18rem;

    margin:1rem 0rem 1rem 0rem;
    height: 2rem;
    display: flex;
}

#addDialog #form-btn{
    margin: 0rem ;
}

.project-close-btn{
    width: 2rem;
    margin-left: auto;
    height: 2rem ;
    border: none;
    background-color: transparent;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAGA;IACI,UAAU;IACV,SAAS;AACb;AACA;IACI,aAAa;;AAEjB;AACA;IACI,YAAY;IACZ,6BAA6B;AACjC;AACA;IACI,eAAe;IACf,sBAAsB;IACtB,YAAY;IACZ,aAAa;IACb,cAAc;IACd,gEAAgE;IAChE,2BAA2B;;;AAG/B;AACA;IACI,gBAAgB;IAChB,iBAAiB;IACjB,cAAc;IACd,iBAAiB;AACrB;AACA;IACI,aAAa;IACb,eAAe;AACnB;AACA;IACI,0BAA0B;;IAE1B,mBAAmB;IACnB,kBAAkB;IAClB,gBAAgB;IAChB,qBAAqB;IACrB,cAAc;AAClB;AACA;IACI,iBAAiB;IACjB,oBAAoB;IACpB,aAAa;IACb,uBAAuB;IACvB,yBAAyB;IACzB,WAAW;IACX,mBAAmB;IACnB,sBAAsB;AAC1B;AACA;IACI,cAAc;IACd,4BAA4B;IAC5B,iBAAiB;IACjB,4CAA4C;IAC5C,yBAAyB;IACzB,iBAAiB;IACjB,mBAAmB;IACnB,mBAAmB;IACnB,qBAAqB;IACrB,2DAA2D;;AAE/D;AACA;IACI,aAAa;IACb,6BAA6B;IAC7B,mBAAmB;IACnB,WAAW;IACX,iBAAiB;AACrB;;AAEA;IACI,iBAAiB;IACjB,iBAAiB;IACjB,aAAa;IACb,sBAAsB;IACtB,UAAU;AACd;AACA;IACI,gBAAgB;IAChB,iBAAiB;IACjB,iBAAiB;AACrB;AACA;IACI,2DAA2D;AAC/D;AACA;IACI,kEAAkE;IAClE,iCAAiC;IACjC,qBAAqB;IACrB,eAAe;IACf,6BAA6B;IAC7B,oCAAoC;IACpC;MACE;KACD,qBAAqB;;IAEtB;AACJ;IACI,cAAc;IACd;MACE;KACD,qBAAqB;;;AAG1B;;AAEA;IACI,iBAAiB;IACjB,2BAA2B;IAC3B,UAAU;IACV,0BAA0B;AAC9B;AACA;IACI,UAAU;IACV,eAAe;IACf,mBAAmB;IACnB,0BAA0B;IAC1B,0BAA0B;;AAE9B;AACA;IACI,eAAe;IACf,YAAY;IACZ,WAAW;;AAEf;AACA;IACI,gBAAgB;IAChB,YAAY;IACZ,eAAe;;AAEnB;AACA;IACI,mBAAmB;IACnB,aAAa;IACb,YAAY;IACZ,uBAAuB;IACvB,sBAAsB;IACtB,mBAAmB;IACnB,cAAc;IACd,eAAe;IACf,QAAQ;IACR,SAAS;IACT,gCAAgC;;;AAGpC;AACA;IACI,2BAA2B;;AAE/B;;AAEA;IACI,4BAA4B;IAC5B,kBAAkB;;AAEtB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;;IAEI,qBAAqB;IACrB,YAAY;IACZ,iCAAiC;IACjC,oCAAoC;AACxC;AACA;IACI,YAAY;IACZ,UAAU;IACV,cAAc;AAClB;AACA;IACI,YAAY;IACZ,aAAa;IACb,4CAA4C;IAC5C,mCAAmC;IACnC,qBAAqB;AACzB;AACA;IACI,kCAAkC;AACtC;;;AAGA;IACI,mBAAmB;IACnB,kBAAkB;IAClB,qBAAqB;;AAEzB;AACA;IACI,gBAAgB;IAChB,gBAAgB;IAChB,kBAAkB;IAClB,aAAa;IACb,UAAU;AACd;AACA;;;;;;;;;GASG;AACH;IACI,oCAAoC;IACpC,kCAAkC;IAClC,aAAa;IACb,cAAc;IACd,oBAAoB;IACpB,qBAAqB;IACrB,YAAY;IACZ,WAAW;IACX,wFAAwF;;AAE5F;AACA;IACI,wCAAwC;IACxC,aAAa;IACb,cAAc;IACd,oBAAoB;IACpB,qBAAqB;I